import * as yup from 'yup';

const validationSchema = yup.object().shape({
  oldpassword: yup
    .string()
    .min(6, '   6 ')
    .max(30, '   30 ')
    .required(' '),
  password: yup
    .string()
    .min(6, '   6 ')
    .max(30, '   30 ')
    .required(' '),
  passwordConfirmation: yup
    .string()
    .oneOf([yup.ref('password'), null], '  ')
    .min(6, '   6 ')
    .max(30, '   30 ')
    .required(' '),
});

export default validationSchema;
