import * as yup from 'yup';

const validationSchema = yup.object().shape({
  email: yup
    .string()
    .email('   ')
    .max(254, '   254 ')
    .required('   . '),
  password: yup
    .string(' ')
    .min(6, '   6 ')
    .max(30, '   30 ')
    .required(' '),
});

export default validationSchema;
